(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase6Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                DATE_FORMAT,
                PURCHASE_PAYLOAD,
                TRAVEL_TYPE,
                PurchaseFactory,
                PurchaseService,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.proceed = function() {
      PurchaseFactory.purchase(generatePayload()).then(function (data) {
        var policyNumber = data.data.PolicyID;
        PurchaseService.policy.number = policyNumber;
        PolicyService.policies = [{
          number: policyNumber
        }];
        $state.go('purchase-7');
      });
    };

    function generatePayload() {
      var payload = angular.copy(PURCHASE_PAYLOAD);
      var policy = PurchaseService.policy;

      payload.Header.Agent = policy.agentCode;
      payload.Header.Product = 'coherent:' + policy.type;
      payload.Header.Email = policy.email;
      payload.Header.InstallmentPremium = policy.specs.planType.price;
      payload.Header.Mandate.CreditCard.CardNumber = vm.ui.cardNumber;
      payload.Header.Mandate.CreditCard.ExpiryMonth = vm.ui.expiryMM;
      payload.Header.Mandate.CreditCard.ExpityYear = vm.ui.expiryYY;
      payload.Header.Owner.Contact.Add.Add1 = policy.address;
      payload.Header.Owner.Contact.Add.Pcode = policy.postalCode1 + '-' + policy.postalCode2;
      payload.Header.Owner.Contact.Email = policy.email;
      payload.Header.Owner.Contact.FbId = policy.fbId;
      payload.Header.Owner.Contact.Phone = policy.contact;
      payload.Header.Owner.Dob = moment(policy.birthDate).format(DATE_FORMAT);
      payload.Header.Owner.Gender = policy.gender;
      payload.Header.Owner.Name = policy.name;
      payload.Header.StartDate = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.Risks[0].CoverType = 'coherent:' + policy.type;
      payload.Risks[0].RiskBody.InsuredParty.Dob = moment(policy.birthDate).format(DATE_FORMAT);
      payload.Risks[0].RiskBody.InsuredParty.Gender = policy.gender;
      payload.Risks[0].RiskBody.InsuredParty.Name = policy.name;
      payload.Risks[0].RiskBody.Plan = policy.specs.planType.code;
      payload.Risks[0].RiskBody.Risk_Comm = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.Risks[0].Risk_Comm = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      return payload;
    }

    function init() {
      if (PurchaseService.policy.type === ACCIDENT_TYPE && !PurchaseService.policy.relationship) {
        $log.debug('relationship is not defined. redirecting to step 1');
        $state.go('main');
        return;
      } else if (PurchaseService.policy.type === TRAVEL_TYPE && !PurchaseService.policy.email) {
        $log.debug('email is not defined. redirecting to products');
        $state.go('products');
        return;
      }
    }

    init();

    return vm;
  }
})();

