(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy3Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                ACCIDENT_TYPE,
                CANCEL_PAYLOAD,
                DATE_FORMAT,
                TRAVEL_TYPE,
                AccidentPurchaseFactory,
                PolicyFactory,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {};

    vm.cancelRenewal = function () {
      PolicyFactory.cancel(generatePayload()).then(function () {
        $state.go('policy-9', {
          policyNumber: vm.ui.policyNumber,
          mode: 'cancel-success',
          cancelDate: vm.ui.riskEnd
        });
      });
    }

    function generatePayload() {
      var payload = angular.copy(CANCEL_PAYLOAD);

      payload.PolicyID = vm.ui.policyNumber;
      payload.TranDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.CancelDate = vm.ui.riskEnd;
      payload.CancelReason = 'Cancellation of Renewal';

      return payload
    }

    function init() {
      PolicyService.debug();

      vm.ui.policyNumber = $stateParams.policyNumber;
      if (!vm.ui.policyNumber) {
        $log.debug('policyNumber is not defined. redirecting to step 1');
        $state.go('policy-1');
      }

      PolicyService.resetPolicy();

      PolicyService.policy.number = vm.ui.policyNumber;

      PolicyFactory.get(vm.ui.policyNumber).then(function (data) {
          $log.debug(data);
          var cont = data.data.Cont;
          var owner = cont.Owner;
          var contact = owner.Contact;
          var risks = cont.Risks['1'];
          var riskBody = risks.RiskBody;
          var insuredParty = riskBody.InsuredParty;

          if (cont.Product === 'coherent:' + ACCIDENT_TYPE) {
            PolicyService.policy.type = vm.ui.type = ACCIDENT_TYPE;
            vm.ui.planType = _.find(AccidentPurchaseFactory.planTypes, {code: riskBody.Plan });
            vm.ui.planType.components = angular.copy(AccidentPurchaseFactory.planTypeComponents);
            vm.ui.planType.components[0].price = riskBody.Benefit_Limits[0].ClaimLimit;
            vm.ui.planType.components[0].policyLimit = riskBody.Benefit_Limits[0].PolicyLimit;
            vm.ui.planType.components[1].price = riskBody.Benefit_Limits[1].ClaimLimit;
            vm.ui.planType.components[1].policyLimit = riskBody.Benefit_Limits[1].PolicyLimit;
            vm.ui.planType.components[2].price = riskBody.Benefit_Limits[2].DailyLimit;
            vm.ui.planType.components[2].policyLimit = riskBody.Benefit_Limits[2].PolicyLimit;
            vm.ui.planType.components[3].subComponents[0].price = riskBody.Benefit_Limits[3].ClaimLimit;
            vm.ui.planType.components[3].subComponents[0].policyLimit = riskBody.Benefit_Limits[3].PolicyLimit;
            vm.ui.planType.components[3].subComponents[1].price = riskBody.Benefit_Limits[4].ClaimLimit;
            vm.ui.planType.components[3].subComponents[1].policyLimit = riskBody.Benefit_Limits[4].PolicyLimit;
            vm.ui.planType.components[4].price = riskBody.Benefit_Limits[5].ClaimLimit;
            vm.ui.planType.components[4].policyLimit = riskBody.Benefit_Limits[5].PolicyLimit;
            vm.ui.planType.price = riskBody.TotalPremium;
            vm.ui.totalPremium = riskBody.TotalPremium;
          }

          if (cont.Product === 'coherent:' + TRAVEL_TYPE) {
            PolicyService.policy.type = vm.ui.type = TRAVEL_TYPE;
            vm.ui.totalPremium = riskBody.TotalPremium;
          }

          vm.opts = PolicyFactory.opts[PolicyService.policy.type];

          PolicyService.policy.planType = vm.ui.planType;

          vm.ui.contractDate = moment(cont.StartDate).format(DATE_FORMAT);
          PolicyService.policy.contractDate = moment(cont.StartDate);
          PolicyService.policy.name = vm.ui.name = owner.Name;
          PolicyService.policy.birthDate = vm.ui.birthDate = moment(owner.Dob).format(DATE_FORMAT);
          vm.ui.gender = owner.Gender;
          PolicyService.policy.postalCode1 = contact.Add.Pcode.substring(0, contact.Add.Pcode.indexOf('-'));
          PolicyService.policy.postalCode2 = contact.Add.Pcode.substring(contact.Add.Pcode.indexOf('-') + 1, contact.Add.Pcode.length);
          vm.ui.postalCode = PolicyService.policy.postalCode1 + '-' + PolicyService.policy.postalCode2;
          PolicyService.policy.address = vm.ui.address = contact.Add.Add1;
          PolicyService.policy.email = vm.ui.email = contact.Email;
          PolicyService.policy.contact = vm.ui.contact = contact.Phone;

          PolicyService.policy.insuredName = vm.ui.insuredName = insuredParty.Name;
          vm.ui.insuredKinship = insuredParty.Kinship;
          PolicyService.policy.insuredBirthDate = vm.ui.insuredbirthDate = moment(insuredParty.Dob).format(DATE_FORMAT);
          PolicyService.policy.insuredGender = vm.ui.insuredGender = insuredParty.Gender;
          vm.ui.paymentDetails = " -  -  - " + cont.Mandate.CreditCard.CardNumber.substring('15', '19');

          PolicyService.policy.allocated = cont.PremiumCollection[0].Premium['1'].Allocated;
          PolicyService.policy.renewalDate = vm.ui.renewalDate = moment(cont.EndDate).format(DATE_FORMAT);
          vm.ui.riskEnd = moment(risks.Risk_End).format(DATE_FORMAT);
        }, function () {
          $log.debug('policy has error. redirecting to step 1');
          $state.go('policy-1');
        });
    }

    init();

    return vm;
  }

})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy2Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                ACCIDENT_TYPE,
                TRAVEL_TYPE,
                AccidentPurchaseFactory,
                PolicyFactory,
                PolicyService) {
    var vm = this

    vm.ui = {
      name: 'Mark Banaria'
    };

    vm.proceed = function(policyNumber) {
      $state.go('policy-3', {
        policyNumber: policyNumber
      });
    };

    var init = function() {
      PolicyService.debug();

      if (PolicyService.policies.length === 0) {
        $log.debug('policies is empty. redirecting to step 1');
        $state.go('policy-1');
      }

      vm.ui.policies = PolicyService.policies;

      angular.forEach(vm.ui.policies, function (policy) {
        PolicyFactory.get(policy.number).then(function (data) {
          var cont = data.data.Cont;
          var risks = cont.Risks['1'];

          policy.type = cont.Product.replace(/^coherent:/, '');
          policy.opts = PolicyFactory.opts[policy.type];
          if(policy.type === ACCIDENT_TYPE) {
            policy.planType = _.find(AccidentPurchaseFactory.planTypes, {
              code: risks.RiskBody.Plan
            });
          }
          if(policy.type === TRAVEL_TYPE) {
            $log.debug('c', cont);
            $log.debug('r', risks);
          }
          policy.amount = risks.RiskBody.TotalPremium;
        }, function () {
          $log.debug('policy has error. redirecting to step 1');
          $state.go('policy-1');
        });
      });
    }

    init();

    return vm;

  }
})();

